import React,{useState} from 'react'
import {Link} from 'react-router-dom'

import './Join.css'

function Join() {
    const [name,setName] = useState('');
    const [room,setRoom] = useState('');

    return (
        <div className="Outer">
            <div className="Inner">
                <h1 className="heading">Join</h1>
                <div><input placeholder="New" className="Join" type="text" onChange={(event)=>setName(event.target.value)}/></div>
                <div><input placeholder="Room" className="Join mt-20" type="text" onChange={(event)=>setRoom(event.target.value)}/></div>
                <Link onClick={e=>(!name||!room)? e.preventDefault() : null} to={`/chat?name=${name}&room=${room}`}>
                    <button className="button mt-20" type="submit">Sign In</button>
                </Link>

            </div>
            
        </div>
    )
}

export default Join
